import Shortcuts from './nw-shortcuts'
import Menubar from './nw-menubar'

const IS_NW = (typeof nw !== 'undefined' && typeof require !== 'undefined')

/* global nw */
export default {
  install (Vue) {
    /**
     * Global nw window instance
     * @type {{}}
     * @private
     */
    let _win = {}

    if (IS_NW) {
      _win = nw.Window.get()
      Vue.prototype.$nwWindow = _win

      Shortcuts.registerGlobalHotKeys()
      Menubar.init()

      /**
       * Global nw window close handler
       * Unregister global HotKeys & close app
       */
      _win.on('close', () => {
        _win.hide()
        Shortcuts.unregisterGlobalHotKeys()
        setTimeout(() => {
          _win.close(true)
        }, 500)
      })
    }

    /**
     * Is nw app
     * @returns {boolean}
     */
    Vue.prototype.$isNw = () => {
      return IS_NW
    }
  }
}
