import * as types from './mutation-types'
import { localStorage } from '../store/utils'

const mutations = {
  /**
   * Change locale, save to local storage & commit i18n mutation
   * @param state
   * @param event
   */
  [types.CHANGE_LOCALE] (state, event) {
    const locale = event.target.text.trim().toLowerCase()
    localStorage.save('locale', locale)
    this.commit('i18n/SET_LOCALE', { locale: locale })
  }
}

export default mutations
