<template>
    <b-navbar type="dark" variant="success">
        <b-navbar-nav>
            <b-nav-item to="/" vatiant="link">Home</b-nav-item>
            <b-nav-item to="/about" vatiant="link">About</b-nav-item>
        </b-navbar-nav>
        <b-navbar-nav class="ml-auto">
            <b-nav-item-dropdown :text="dropDownLabel"
                                 variant="success"
                                 right>
                <b-dropdown-item v-for="locale in locales"
                                 :key="locale"
                                 @click="changeLocale">
                    {{ locale.toUpperCase() }}
                </b-dropdown-item>
            </b-nav-item-dropdown>
        </b-navbar-nav>
    </b-navbar>
</template>

<script>
import { mapMutations } from 'vuex'
import { CHANGE_LOCALE } from '../store/mutation-types'

export default {
  name: 'NavBar',
  data () {
    return {
      locales: this.$i18n.locales()
    }
  },
  computed: {
    dropDownLabel () {
      return 'Language: ' + this.$i18n.locale().toUpperCase()
    }
  },
  methods: {
    ...mapMutations({
      changeLocale: CHANGE_LOCALE
    })
  }
}
</script>

<style lang="scss">
.navbar {
  a {
    font-weight: bold;
    &.router-link-exact-active {
      color: #ffffff !important;
    }
  }
}
</style>
